@tailwind base;
@tailwind components;
@tailwind utilities;

@import url("https://fonts.googleapis.com/css2?family=Poppins&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@700&display=swap");

::-webkit-scrollbar {
  width: 5px;
  background-color: #212121;
}

::-webkit-scrollbar-thumb {
  background-color: #ffc20d;
  border-radius: 5px;
}

* {
  font-family: "Poppins", sans-serif;
}

.main-heading {
  font-family: "Montserrat", sans-serif;
}

video, .hero-image {
  object-fit: cover;
  width: 100vw;
  height: 100vh;
  top: 0;
  left: 0;
}

body {
  background-color: #272727;
  color: #fff;
}

/* Modal container */
.ReactModal__Overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

/* Scrollable modal content */
.ReactModal__Content {
  overflow-y: auto;
  color: white;
  border-radius: 10px;
}

/* Navbar Mobile */
#menuToggle {
  display: block;
  padding: 2rem;
  z-index: 1;
  -webkit-user-select: none;
  user-select: none;
}

#menuToggle a {
  transition: color 0.3s ease;
}

#menuToggle input {
  display: block;
  width: 40px;
  height: 32px;
  position: absolute;
  cursor: pointer;
  opacity: 0;
  z-index: 2;
}

#menu .list {
  padding: 1rem 0;
  font-size: 20px;
}

#menuToggle span {
  display: block;
  width: 33px;
  height: 4px;
  margin-bottom: 5px;
  position: relative;
  background: #ffc20d;
  border-radius: 3px;
  z-index: 1;
  transform-origin: 4px 0px;
  transition: transform 0.5s cubic-bezier(0.77, 0.2, 0.05, 1),
    background 0.5s cubic-bezier(0.77, 0.2, 0.05, 1), opacity 0.55s ease;
}

#menuToggle input:checked ~ span {
  opacity: 1;
  transform: rotate(45deg) translate(-2px, -1px);
  background: #ffc20d;
}

#menuToggle input:checked ~ span:nth-last-child(3) {
  opacity: 0;
  transform: rotate(0deg) scale(0.2, 0.2);
}

#menuToggle input:checked ~ span:nth-last-child(2) {
  transform: rotate(-45deg) translate(0, -1px);
}

#menu {
  display: flex;
  flex-direction: column;
  position: absolute;
  width: 100vw;
  padding-top: 5rem;
  padding-left: 2rem;
  z-index: 0;
  top: 0;
  left: 0;
  height: 100vh;
  list-style-type: none;
  background: #272727;
  transform-origin: 0% 0%;
  transform: translate(-140%, 0);
  transition: transform 0.5s cubic-bezier(0.77, 0.2, 0.05, 1);
}

#menuToggle input:checked ~ ul {
  transform: none;
}

/* Puzzle Game - Updated styles */
.puzzle-game {
  background-color: #272727; /* Ensures background matches global style */
  color: #fff; /* Ensures text color is white (matches body color) */
  text-align: center;
  border-radius: 12px;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.1);
  padding: 30px;
  width: 80%;
  max-width: 1000px;
  margin-top: 80px;
}

.puzzle-game h1, .puzzle-game h2 {
  font-family: 'Montserrat', sans-serif;
}

.puzzle-game h1 {
  font-size: 36px;
  color: #ffc20d; /* Using your base yellow color */
  margin-bottom: 10px;
}

.puzzle-game h2 {
  font-size: 24px;
  color: #ffc20d; /* Color matches other headings */
  margin-bottom: 20px;
}

.puzzle-game .message {
  font-size: 18px;
  font-weight: bold;
  color: #ff6347;
  margin: 10px 0;
}

/* Difficulty Controls */
.puzzle-game .difficulty-controls button {
  background: #ffc20d;
  color: white;
  border: none;
  border-radius: 8px;
  padding: 10px 20px;
  margin: 5px;
  font-size: 16px;
  cursor: pointer;
  transition: background-color 0.3s ease, transform 0.2s ease;
}

.puzzle-game .difficulty-controls button:hover {
  background-color: #ff9900;
  transform: scale(1.1);
}

.puzzle-game .difficulty-controls button:active {
  transform: scale(0.95);
}

/* Matrix Grid */
.puzzle-game .matrix {
  display: grid;
  grid-template-columns: repeat(10, 60px);
  grid-template-rows: repeat(10, 60px);
  gap: 5px;
  margin-top: 20px;
  justify-content: center;
}

.puzzle-game .matrix-cell {
  width: 60px;
  height: 60px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: linear-gradient(145deg, #f4f4f4, #e0e0e0);
  border: 1px solid #ccc;
  border-radius: 8px;
  font-size: 16px;
  font-weight: bold;
  color: #333;
  cursor: pointer;
  transition: all 0.3s ease;
}

.puzzle-game .matrix-cell:hover {
  background-color: #add8e6;
  transform: scale(1.1);
}

.puzzle-game .matrix-cell.selected {
  background-color: #66c2ff;
  color: white;
  transform: scale(1.1);
  transition: all 0.3s ease;
}

/* Correct word - Highlighted and unselectable */
.puzzle-game .matrix-cell.correct {
  background-color: #66c2ff;  /* Highlight correct cells */
  color: white;
  cursor: not-allowed;  /* Disable further selection */
  transform: scale(1.1);
}

.puzzle-game .matrix-cell.selected.correct {
  background-color: #66c2ff; /* Keep the correct word highlighted */
  color: white;
  cursor: not-allowed; /* Ensure it can't be selected again */
}

/* Controls Buttons */

/* Selected word highlight */
.puzzle-game .matrix-cell.highlighted {
  border: 3px solid red; /* Thicker red border for highlighted word */
}

/* Selected word highlight */
.puzzle-game .matrix-cell.selected {
  background-color: #ff6347; /* Highlight selected cells */
}

.puzzle-game .controls button {
  background-color: #ffc20d;
  color: white;
  border: none;
  border-radius: 8px;
  padding: 10px 20px;
  margin: 10px 5px;
  cursor: pointer;
}

.puzzle-game .controls button:disabled {
  background-color: #cccccc;
}

.puzzle-game .controls button:hover {
  background-color: #ff9900;
}

/* Responsive Design */
@media (max-width: 768px) {
  .puzzle-game {
    width: 95%;
    padding: 20px;
  }

  .puzzle-game h1 {
    font-size: 28px;
  }

  .puzzle-game h2 {
    font-size: 20px;
  }

  .puzzle-game .matrix {
    grid-template-columns: repeat(5, 60px); /* Adjust grid for smaller screens */
    grid-template-rows: repeat(5, 60px);
  }
}